
import { defineComponent } from 'vue';
import * as echarts from 'echarts';
import MemorySnapshotTable from './memory-snapshot-table.vue';
import * as MemoryData from './memory-data';
import MemoryCompareTables from './memory-compare-tables.vue';
// import { showToast, TipsType } from '@/utils/toast';
import { memoryChannel as channel } from '../memory-channel';
import { TdfCommand } from '@tencent/tdf-devtools-protocol/types/enum-tdf-mapping';

// const coreSnapshotPrefix = 'memory_core_snapshot';
// const jsSnapshotPrefix = 'memory_js_snapshot';
const timeShowSpit = ':';
// const timeFileNameSplit = '_';
let memoryChart: echarts.ECharts;
let option: echarts.EChartsOption;
let timer: NodeJS.Timeout;
let i = 0;
let clickParams: MemoryData.IMemoryChartPointerClickParams;
const heapDataList = Array(100);

export default defineComponent({
  components: { MemorySnapshotTable, MemoryCompareTables },
  data() {
    return {
      snapshotTable: [],
      // echarts
      time: Array(100).fill(''),
      // echarts
      coreData: Array(100),
      selectedTime: '',
      isRecording: false,
      isLoadingSnapshot: false,
      memoryExpandedArray: [MemoryData.CORE_MEMORY_STRING],
    };
  },
  watch: {
    '$store.state.device.list'(appConnect) {
      console.log('list', appConnect);
      this.onDeviceChange();
    },
    '$store.state.device.appConnect'(appConnect) {
      console.log('appConnect', appConnect);
      this.onAppStatusChange();
    },
  },
  // 
  mounted() {
    this.drawEChart();
  },
  destroyed() {
    console.log('memory page destroyed');
    // this.closeWebSockerConnect();
    // this.deleteCacheFile();
  },
  methods: {
    drawEChart() {
      const charts = this.$refs.memoryChartContainer as HTMLElement;
      if (charts !== null) {
        if (memoryChart) memoryChart.dispose();
        memoryChart = echarts.init(charts);
      }
      option = {
        dataZoom: [
          {
            filterMode: 'none',
            realtime: false, // 
            height: 25, // 
            start: 75, // 100
            end: 100, // 100
          },
        ],
        title: {},
        tooltip: {
          triggerOn: 'click',
          trigger: 'axis',
          enterable: true,
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#909399', // 
            },
          },
        }, // 
        legend: {
          show: true,
          data: [MemoryData.CORE_MEMORY_STRING],
          icon: 'circle',
          // orient: 'vertical',
          // right: 10,
          textStyle: {
            fontSize: 14,
          },
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          axisPointer: {
            value: '',
            snap: true,
            lineStyle: {
              color: '#409EFF',
              width: 1,
            },
            label: {
              formatter: (params: MemoryData.IMemoryChartPointerClickParams) => this.onAxisPointerClick(params),
            },
            handle: {
              show: true,
              color: '#409EFF',
              icon: 'none',
            },
          },
          axisLabel: {
            interval: 1,
          },
          data: this.time,
        },
        yAxis: {
          type: 'value',
          scale: true,
          show: true,
          axisLabel: {
            formatter: '{value} M',
          },
        },
        series: [
          {
            name: MemoryData.CORE_MEMORY_STRING,
            type: 'line',
            // smooth: true,
            lineStyle: {
              width: 1,
            },
            symbol: 'none',
            // areaStyle: {},
            data: this.coreData,
            color: '#67C23A',
          },
        ],
      };
      memoryChart.setOption(option);
      memoryChart.getZr().on('click', () => {
        const { seriesData } = clickParams;
        if (clickParams.axisDimension === 'x') {
          this.selectedTime = (seriesData[0] as any).name;
          this.onValueClick(clickParams.value);
        } else if (clickParams.axisDimension === 'y') {
          this.onValueClick(seriesData[0].value);
        }
      });
      // memoryChart.on('brushSelected', (params: echarts.Payload) => this.onBrushSelected(params));
      window.onresize = function () {
        memoryChart.resize();
      };
    },
    onAxisPointerClick(params: MemoryData.IMemoryChartPointerClickParams) {
      clickParams = params;
      return params.value;
    },
    addMemoryData(id, time: string, yValue: number) {
      this.time.push(time);
      this.time.shift();
      heapDataList.push({ id, time });
      heapDataList.shift();

      this.coreData.push(yValue);
      this.coreData.shift();

      this.updateEchartsData();
    },
    updateEchartsData() {
      memoryChart.setOption({
        xAxis: {
          data: this.time,
        },
        series: [
          {
            name: MemoryData.CORE_MEMORY_STRING,
            type: 'line',
            smooth: true,
            lineStyle: {
              width: 1,
            },
            symbol: 'none',
            data: this.coreData,
          },
        ],
      });
    },
    onValueClick(time: string) {
      memoryChart.setOption({
        xAxis: {
          axisPointer: {
            value: time,
          },
        },
      });
      // tabtimetab
      this.loadTabData(time);
    },
    loadTabData(time: string) {
      this.fetchHeapMeta(time);
    },
    async fetchHeapMeta(time) {
      const item = heapDataList.find((v) => v && v.time === time);
      if (item) {
        const { id } = item;
        channel
          .send({
            method: 'TDFMemory.fetchHeapCache',
            params: { id },
          } as any)
          .then((res) => {
            const commandRes = res as Adapter.CDP.CommandRes;
            console.log(commandRes);
            this.snapshotTable = this.groupHeapMates(time, commandRes.result.heapMetas) as any;
          });
      }
    },
    goToDetail() {
      return;
    },
    startRecord() {
      // this.openFile();
      timer = setInterval(this.doSendCoreRequest, 1000);
      this.isRecording = true;
    },
    stopCollectMemory() {
      clearInterval(timer);
      this.isRecording = false;
    },
    onReceivedMessage(id, rsp: MemoryData.IMemoryJsonData) {
      const sumByte = rsp.heapMetas.reduce(
        (accumulator: number, currentValue: MemoryData.IMemoryHeapMeta) =>
          accumulator + Number((currentValue as any).s || 0),
        0
      );
      const memoryMSize = Number((sumByte / 1024 / 1024).toFixed(2));
      console.log(`onReceivedMessage core memorySize: ${memoryMSize}`);
      i = i + 1;
      const date = new Date();
      const time = [date.getHours(), date.getMinutes(), date.getSeconds()].join(timeShowSpit);
      this.addMemoryData(id, time, memoryMSize);
    },
    doSendCoreRequest() {
      channel
        .send({
          method: TdfCommand.TDFMemoryGetHeapMeta,
          params: {},
        } as any)
        .then((res) => {
          const commandRes = res as Adapter.CDP.CommandRes;
          this.onReceivedMessage(commandRes.id, commandRes.result as MemoryData.IMemoryJsonData);
        });
    },
    groupHeapMates(rowKey: string, heapMetas: Array<MemoryData.IMemoryHeapMeta>): Array<MemoryData.MemoryTableMeta> {
      const groups = heapMetas
        .map((val) => (val as any).t)
        .reduce((acc: { [key: string]: Array<MemoryData.IMemoryHeapMeta> }, val, i) => {
          acc[val] = (acc[val] || []).concat(heapMetas[i]);
          return acc;
        }, {});

      const result = new Array<MemoryData.MemoryTableMeta>();
      let key: string;
      for (key in groups) {
        const value: Array<MemoryData.IMemoryHeapMeta> = groups[key];
        const size = value.reduce(
          (accumulator: number, currentValue: MemoryData.IMemoryHeapMeta) =>
            accumulator + Number((currentValue as any).s || 0),
          0
        );
        const object: MemoryData.MemoryTableMeta = {
          size,
          count: value.length,
          type: key,
          detail: value,
          key: rowKey,
        };
        result.push(object);
      }
      return result;
    },
    clearDatas() {
      this.time = Array(100).fill('');
      this.coreData = Array(100);
      this.selectedTime = '';
      this.updateEchartsData();
      clearInterval(timer);
    },
    onMemoryExpandChanged(active: string) {
      console.log('onMemoryExpandChanged', active);
    },
    onDeviceChange() {
      console.log(`[memory] onDeviceChange`);
      this.stopCollectMemory();
    },
    onAppStatusChange() {
      console.log(`[memory] onAppStatusChange`);
      this.stopCollectMemory();
    },
  },
});
