export class Channel {
  private ws: WebSocket;
  private msgBuffer: Channel.RequestData[] = [];
  private requestPromiseMap: Map<string | number, any> = new Map();
  private eventListenerMap: Map<string, Channel.EventListener[]> = new Map();

  get isReady() {
    return this.ws.readyState === WebSocket.OPEN;
  }

  constructor(extensionName: string) {
    const url = new URL(window.parent.location.href);
    const newUrl = new URL(url.searchParams.get('ws') as string);
    const clientId = newUrl.searchParams.get('clientId');
    newUrl.searchParams.set('clientId', `${clientId}-${extensionName}`);
    const wsUrl = `ws://${newUrl.toString()}`;
    console.log(wsUrl);

    this.ws = new WebSocket(wsUrl);

    this.ws.onopen = () => {
      console.log('ws connected!');
      this.msgBuffer.forEach((msg) => {
        this.send(msg);
      });
      this.msgBuffer = [];
    };

    this.ws.onmessage = (event) => {
      const res = JSON.parse(event.data) as Adapter.CDP.Res;
      if (!('id' in res)) {
        return;
      }
      const { id } = res;
      const requestPromise = this.requestPromiseMap.get(id);
      if (requestPromise) {
        requestPromise.resolve(res);
        this.requestPromiseMap.delete(id);
      }
    };

    this.ws.onclose = () => {
      const e = new Error('ws closed');
      for (const requestPromise of this.requestPromiseMap.values()) {
        requestPromise.reject(e);
      }
    };
  }

  public send(msg: Channel.RequestData): Promise<Adapter.CDP.Res> {
    return new Promise((resolve, reject) => {
      if (this.isReady) {
        sendToParent('getRequestId').then((id) => {
          msg.id = id as number;
          this.requestPromiseMap.set(msg.id, { resolve, reject });
          return this.ws.send(JSON.stringify(msg));
        });
      }
      this.msgBuffer.push(msg);
    });
  }

  public addEventListener(method: string, listener: Channel.EventListener) {
    if (!this.eventListenerMap.has(method)) this.eventListenerMap.set(method, []);
    this.eventListenerMap.get(method)?.push(listener);
  }
}

export const sendToParent = (cmd: string) =>
  new Promise((resolve, reject) => {
    window.parent.postMessage(cmd, window.location.origin);
    const listener = (event: any) => {
      window.removeEventListener('message', listener);
      resolve(event.data);
    };
    window.addEventListener('message', listener, false);

    setTimeout(() => {
      reject(new Error('no response, timeout!'));
    }, 2000);
  });
