
import { defineComponent } from 'vue';
import * as MemoryData from './memory-data';

export default defineComponent({
  name: 'MemoryComparedTables',
  props: ['source'],
  data() {
    return {
      startOnlyDetailTableData: Array<MemoryData.IMemoryHeapMeta>(),
      // bothDetailTableData: Array<MemoryData.IMemoryHeapMeta>(),
      endOnlyDetailTableData: Array<MemoryData.IMemoryHeapMeta>(),
    };
  },
  methods: {
    onStartOnlyExpandChange(row: MemoryData.MemoryTableMeta, expandedRows: Array<MemoryData.MemoryTableMeta>) {
      if (expandedRows.length === 0) {
        this.startOnlyDetailTableData = [];
      } else {
        this.startOnlyDetailTableData = row.detail;
      }
    },
    // onBothExpandChange(row: MemoryData.MemoryTableMeta, expandedRows: Array<MemoryData.MemoryTableMeta>) {
    //   if (expandedRows.length === 0) {
    //     this.bothDetailTableData = [];
    //   } else {
    //     this.bothDetailTableData = row.detail;
    //   }
    // },
    onEndOnlyExpandChange(row: MemoryData.MemoryTableMeta, expandedRows: Array<MemoryData.MemoryTableMeta>) {
      if (expandedRows.length === 0) {
        this.endOnlyDetailTableData = [];
      } else {
        this.endOnlyDetailTableData = row.detail;
      }
    },
    onStartOnlyRowClick(row: MemoryData.MemoryTableMeta) {
      this.startOnlyDetailTableData = row.detail;
    },
    // onBothRowClick(row: MemoryData.MemoryTableMeta) {
    //   this.bothDetailTableData = row.detail;
    // },
    onEndOnlyRowClick(row: MemoryData.MemoryTableMeta) {
      this.endOnlyDetailTableData = row.detail;
    },
    getRowKey(row: MemoryData.MemoryTableMeta) {
      return `${row.key}-${row.type}`;
    },
  },
});
